import React from "react";
const queryString = require('query-string');

class AddressVerification extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      checkId: null,
      redirectUrl: null
    };
  }

  componentDidMount() {
    const qs = this.props.location.search;
    const tokenisedQs = queryString.parse(qs);

    if (!tokenisedQs.customerInternalReference) {
      console.log('No check ID');
      return;
    }

    const checkId = tokenisedQs.customerInternalReference;
    this.setState({
      checkId,
    });

    const postData = {
      'customer_id': checkId
    };

    // fetch('http://localhost:3000/api/address-checks/' + checkId, {
    fetch('https://stage2-api-jumio.eng.lendinvestinternal.com/api/address-checks/' + checkId, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(postData)
    })
    .then((response) => {
      console.log(response);
      return response.json();
    })
    .then((data) => {
      console.log(data);
      window.location = data.data.client_redirect_url;
    })
    .catch((error) => {
      console.log(error);
    });
  }

  render() {
    return <p>Getting Proof of Address rediect URL for {this.state.checkId}</p>;
  }
}

export default AddressVerification;
