import React from "react";
const queryString = require('query-string');

class VerificationComplete extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const qs = this.props.location.search;
    console.log(qs);
    const tokenisedQs = queryString.parse(qs);
    console.log(tokenisedQs);
  }

  render() {
    const tokenisedQs = queryString.parse(this.props.location.search);
    const id = tokenisedQs.jumioIdScanReference;
    return <p>Verification complete for {id}</p>;
  }
}

export default VerificationComplete;
