import React from "react";
const queryString = require('query-string');

class IdVerification extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      checkId: null,
      redirectUrl: null
    };
  }

  componentDidMount() {
    const qs = this.props.location.search;
    const tokenisedQs = queryString.parse(qs);

    if (!tokenisedQs.checkId) {
      console.log('No check ID');
      return;
    }

    const checkId = tokenisedQs.checkId;
    this.setState({
      checkId,
    });

    // fetch('http://localhost:3000/api/netverify/initiate/redirecturl/' + checkId, {
    fetch('https://stage2-api-jumio.eng.lendinvestinternal.com/api/netverify/initiate/redirecturl/' + checkId, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
    .then((response) => {
      console.log(response);
      return response.json();
    })
    .then((data) => {
      console.log(data);
      this.setState({
        ...this.state,
        redirectUrl: data.data
      });
    })
    .catch((error) => {
      console.log(error);
    });
  }

  render() {
    if (this.state.redirectUrl) {
      return <div><iframe src={this.state.redirectUrl}></iframe></div>;
    }

    if (this.state.checkId) {
      return <p>Getting ID/Selfie rediect URL for {this.state.checkId}</p>;
    }

    return <p>Error - No CheckId Provided</p>;
  }
}

export default IdVerification;
