import React from "react";
import {
  //HashRouter as Router,
  StaticRouter as Router,
  Switch,
  Route,
  useLocation,
} from "react-router-dom";
import IdVerification from "./components/IdVerification.js";
import AddressVerification from "./components/AddressVerification.js";
import VerificationComplete from "./components/VerificationComplete.js";
import VerificationError from "./components/VerificationError.js";

export default function App() {
  // console.log(document.location);
  // const base = document.location.pathname.trimStart('/').split('/').pop();
  // console.log(base);
  return (
    <Router location={document.location}>
        <Switch>
          <Route path="/error.html" component={VerificationError} />
          <Route path="/verification-complete.html" component={VerificationComplete} />
          <Route path="/address-verification.html" component={AddressVerification} />
          <Route path="/verification.html" component={IdVerification} />

          <Route path="/api-jumio/error.html" component={VerificationError} />
          <Route path="/api-jumio/verification-complete.html" component={VerificationComplete} />
          <Route path="/api-jumio/address-verification.html" component={AddressVerification} />
          <Route path="/api-jumio/verification.html" component={IdVerification} />
          <Route>
            <NoMatch />
          </Route>
        </Switch>
    </Router>
  );
}

const NoMatch = () => {
  let location = useLocation();

  return (
    <div>
      <h3>
        No match for <code>{location.pathname}</code>
      </h3>
    </div>
  );
}
